import React from 'react'
import { Formik } from 'formik'

function Form({ onFormSubmitSuccess }) {
  const formikValidate = values => {
    let errors = {}
    if (!values.email) {
      errors.email = 'Email is required'
    } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)) {
      errors.email = 'Invalid email address'
    }
    if (!values.firstName) {
      errors.firstName = 'First name is required'
    }
    if (!values.lastName) {
      errors.lastName = 'Last name is required'
    }
    return errors
  }

  const onFormSubmit = (values, { setSubmitting }) => {
    onFormSubmitSuccess(values.firstName, () => {
      values.firstName = ''
      values.lastName = ''
      values.email = ''
    })
    setTimeout(() => {
      setSubmitting(false)
    }, 400)
  }

  return (
    <Formik
      initialValues={{ firstName: '', lastName: '', email: '' }}
      validate={formikValidate}
      onSubmit={onFormSubmit}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        isSubmitting,
      }) => (
        <form onSubmit={handleSubmit}>
          <div className="form-group firstName">
            <label htmlFor="firstName">
              <span>*</span>First Name
            </label>
            <input
              className="event-invite_input"
              name="firstName"
              type="text"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.firstName}
              style={
                errors.firstName && touched.firstName
                  ? { borderColor: '#DD3D4C' }
                  : {}
              }
            />
            <span className="form-error">
              {' '}
              {errors.firstName && touched.firstName && errors.firstName}
            </span>
          </div>
          <div className="form-group lastName">
            <label htmlFor="lastName">
              <span>*</span>Last Name
            </label>
            <input
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.lastName}
              style={
                errors.lastName && touched.lastName
                  ? { borderColor: '#DD3D4C' }
                  : {}
              }
              className="event-invite_input"
              name="lastName"
              type="text"
            />
            <span className="form-error">
              {' '}
              {errors.lastName && touched.lastName && errors.lastName}
            </span>
          </div>
          <div className="form-group email">
            <label htmlFor="name">
              <span>*</span>Email
            </label>
            <input
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.email}
              style={
                errors.email && touched.email
                  ? { borderColor: '#DD3D4C', marginBottom: '10px' }
                  : { marginBottom: '10px' }
              }
              className="event-invite_input"
              name="email"
              type="email"
            />
            <span style={{ bottom: '-10px' }} className="form-error">
              {' '}
              {errors.email && touched.email && errors.email}
            </span>
          </div>
          <input className="submitBtn" value="submit" type="submit" />
        </form>
      )}
    </Formik>
  )
}

export default Form
