import React from 'react'
import FlowerSVG from '../UI/FlowerSVG'
import { Spring, config } from 'react-spring'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowLeft } from '@fortawesome/free-solid-svg-icons'
import { withGoogleMap, GoogleMap, withScriptjs } from 'react-google-maps'

const EventGoogleMap = withScriptjs(
  withGoogleMap(props => (
    <GoogleMap
      defaultCenter={{ lat: 40.7608, lng: -111.876183 }}
      defaultZoom={13}
    />
  ))
)

function Map({ showMainPage }) {
  return (
    <Spring
      config={config.gentle}
      from={{ transform: 'translateY(-100px)' }}
      to={{ transform: 'translateY(0)' }}
    >
      {props => (
        <div style={props}>
          <section className="event-invite_get-there">
            <div className="head">
              <h1>How to Get There</h1>
              <FlowerSVG />
            </div>
            <div className="address">
              <span style={{ display: 'block' }}>Bright Mark's Sanctuary</span>
              <p>500 Terry A Francois Blvd, Salt Lake City, UT 84121, USA</p>
            </div>
            <div onClick={showMainPage} className="back-btn">
              <FontAwesomeIcon icon={faArrowLeft} />
              Back
            </div>
          </section>
          <div
            style={{ width: '100%', height: '500px' }}
            className="event-invite_map"
          >
            <EventGoogleMap
              googleMapURL="https://maps.googleapis.com/maps/api/js?key=AIzaSyDceqaIAkKocZKrfK3_AA-eBR0Wr7hx_RA"
              loadingElement={<div style={{ height: `100%` }} />}
              containerElement={
                <div style={{ height: `500px`, width: '100%' }} />
              }
              mapElement={<div style={{ height: `100%` }} />}
            />
          </div>
        </div>
      )}
    </Spring>
  )
}

export default Map
