import React from 'react'
import { Link } from 'gatsby'
import { Spring } from 'react-spring'
import MenuIcon from './MenuIcon'
function MobileMenu({ pageId, menuOpen, toggleMenu }) {
  return (
    <Spring from={{ opacity: 0 }} to={{ opacity: 1 }}>
      {props => (
        <div style={props} className="event-invite_mobile-menu">
          <header
            style={{ backgroundColor: '#111' }}
            className="event-invite-header"
          >
            <MenuIcon toggleMenu={toggleMenu} menuOpen={menuOpen} />
          </header>
          <ul className="event-invite_mobile-nav">
            <li>
              <Link
                onClick={toggleMenu}
                className={pageId === '' ? 'active' : ''}
                to="/event-invite/"
              >
                Home
              </Link>
            </li>
            <li>
              <Link
                onClick={toggleMenu}
                className={pageId === 'story' ? 'active' : ''}
                to="/event-invite/story"
              >
                Our Story
              </Link>
            </li>
            <li>
              <Link
                onClick={toggleMenu}
                className={pageId === 'wedding' ? 'active' : ''}
                to="/event-invite/wedding"
              >
                The Wedding
              </Link>
            </li>
            <li>
              <Link
                onClick={toggleMenu}
                className={pageId === 'party' ? 'active' : ''}
                to="/event-invite/party"
              >
                Wedding Party
              </Link>
            </li>
            <li>
              <Link
                onClick={toggleMenu}
                className={pageId === 'details' ? 'active' : ''}
                to="/event-invite/details"
              >
                Details
              </Link>
            </li>
          </ul>
        </div>
      )}
    </Spring>
  )
}

export default MobileMenu
