import React, { Component } from 'react'
import Main from './Main'
import Map from './Map'
class Details extends Component {
  state = {
    mapShown: false,
  }

  showMapPage = () => {
    this.setState({
      mapShown: true,
    })
  }

  showMainPage = () => {
    this.setState({
      mapShown: false,
    })
  }

  renderDetailPage = () => {
    const { mapShown } = this.state
    if (mapShown) {
      return <Map showMainPage={this.showMainPage} />
    } else {
      return <Main showMapPage={this.showMapPage} />
    }
  }
  render() {
    return this.renderDetailPage()
  }
}

export default Details
