import React from 'react'

function MenuIcon({ menuOpen, toggleMenu }) {
  const classes = ['hamburger']
  if (menuOpen) {
    classes.push('active')
  }
  const classList = classes.join(' ')

  return (
    <div onClick={() => toggleMenu()} className="event-invite_menuIcon">
      <div className={classList + ' one'} />
      <div className={classList + ' two'} />
      <div className={classList + ' three'} />
    </div>
  )
}

export default MenuIcon
