import React, { Component } from 'react'
import SEO from '../components/Layout/seo'
import Layout from '../components/Layout/layout'
import Header from '../components/EventInvite/Header'
import Footer from '../components/EventInvite/Footer'
import Home from '../components/EventInvite/Home'
import Story from '../components/EventInvite/Story'
import Wedding from '../components/EventInvite/Wedding'
import Party from '../components/EventInvite/Party'
import Details from '../components/EventInvite/Details'
import Register from '../components/EventInvite/Register'
import MobileMenu from '../components/EventInvite/UI/MobileMenu'
import '../components/EventInvite/event-invite.css'
class EventInvite extends Component {
  state = {
    mobile: false,
    mobileMenuOpen: false,
  }

  componentDidMount() {
    if (window.innerWidth < 948) {
      this.setState({
        mobile: true,
      })
    }
    window.addEventListener('resize', () => {
      if (window.innerWidth < 948) {
        this.setState({
          mobile: true,
        })
      } else {
        this.setState({
          mobile: false,
          mobileMenuOpen: false,
        })
      }
    })
  }

  toggleMobileMenu = () => {
    this.setState(prevState => ({
      mobileMenuOpen: !prevState.mobileMenuOpen,
    }))
  }
  renderPage = () => {
    const { pathname } = this.props.location
    const n = pathname.lastIndexOf('/')
    const pageId = pathname.substring(n + 1)

    switch (pageId) {
      case '':
        return <Home />
      case 'story':
        return <Story />
      case 'wedding':
        return <Wedding />
      case 'party':
        return <Party />
      case 'details':
        return <Details />
      case 'register':
        return <Register mobile={this.state.mobile} />
      default:
        return <Home />
    }
  }

  render() {
    const { pathname } = this.props.location
    const n = pathname.lastIndexOf('/')
    const pageId = pathname.substring(n + 1)
    return (
      <Layout classes="event-invite">
        <SEO
          title="Day Two"
          subTitle="Event Invite Theme"
          keywords={[`30 days 30 sites`, `front end challenge`, `react`]}
        />
        <Header
          pageId={pageId}
          mobile={this.state.mobile}
          menuOpen={this.state.mobileMenuOpen}
          toggleMenu={this.toggleMobileMenu}
        />
        {this.state.mobileMenuOpen && this.state.mobile ? (
          <MobileMenu
            toggleMenu={this.toggleMobileMenu}
            menuOpen={this.state.mobileMenuOpen}
            pageId={pageId}
          />
        ) : (
          <div>
            {this.renderPage()}
            <Footer />
          </div>
        )}
      </Layout>
    )
  }
}

export default EventInvite
