import React, { Component } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowLeft } from '@fortawesome/free-solid-svg-icons'
import { Transition } from 'react-spring'
import Form from './Form'
import Card from './Card'
class Register extends Component {
  state = {
    rsvpSuccess: false,
    rsvpName: '',
  }
  onFormSubmitSuccess = (name, callback) => {
    callback()
    this.setState({
      rsvpSuccess: true,
      rsvpName: name,
    })
    setTimeout(() => {
      this.setState({
        rsvpSuccess: false,
      })
    }, 5000)
  }
  render() {
    return (
      <div>
        <Transition
          items={this.state.rsvpSuccess}
          from={{ transform: 'translateY(-300px)' }}
          enter={{ transform: 'translateY(0px)' }}
          leave={{ transform: 'translateY(-300px)' }}
        >
          {rsvpSuccess =>
            rsvpSuccess &&
            (props => (
              <div style={props} className="rsvp-success-card">
                You have successfully reserved your spot, {this.state.rsvpName}{' '}
                🥂
              </div>
            ))
          }
        </Transition>
        <div className="event-invite_register">
          {this.props.mobile && (
            <div className="event-invite_register-mobile-header">
              <h1>We're Engaged</h1>
              <hr />
              <p>Sep 01, 2023, 7:00 PM</p>
              <span style={{ display: 'block' }}>Bright Mark's Sanctuary</span>
            </div>
          )}
          <main className="event-invite_register-main">
            <div onClick={() => window.history.back()} className="back-btn">
              <FontAwesomeIcon icon={faArrowLeft} />
              Back
            </div>
            <div className="head">
              <h1>Add Your Details</h1>
            </div>
            <Form onFormSubmitSuccess={this.onFormSubmitSuccess} />
          </main>
          {!this.props.mobile && <Card />}
        </div>
      </div>
    )
  }
}

export default Register
