import React from 'react'
import Banner from './Banner'
import BgImg from './BgImg'
import { Spring, config } from 'react-spring'

function Wedding() {
  return (
    <Spring config={config.gentle} from={{ opacity: 0 }} to={{ opacity: 1 }}>
      {props => (
        <div style={props}>
          <Banner />
          <BgImg />
        </div>
      )}
    </Spring>
  )
}

export default Wedding
